import axios from 'axios'

const api = axios.create({
  baseURL: 'https://api.yourdictionary.com/',
  headers: {
    'Content-Type': 'application/hal+json',
    Accept: 'application/json'
  }
})

export const getWordsList = async letters => {
  const { data } = await api.get('/wordfinder/v1/unscrambler', {
    params: {
      tiles: letters,
      offset: 0,
      limit: 100,
      order_by: 'alpha',
      augment_results: 'min_5_add_1',
      dictionary: 'WL',
      group_by: 'word_length',
      dictionary_opt: 'YDR'
    }
  })

  return data
}
