<template>
  <div class="wf-WordFinder">
    <v-card class="mx-auto" color="grey-lighten-3" max-width="400">
      <v-card-text>
        <v-text-field
          :loading="isLoading"
          v-model="letters"
          density="compact"
          variant="solo"
          label="Search words"
          append-inner-icon="mdi-magnify"
          hide-details
          single-line
          @click:append-inner="getWords"
        ></v-text-field>
      </v-card-text>
    </v-card>
    <br />
    <v-card class="mx-auto" max-width="400">
      <v-alert
        v-model="showError"
        border="start"
        variant="tonal"
        closable
        close-label="Close Alert"
        color="error accent-4"
      >
        {{ errorText }}
      </v-alert>
    </v-card>
    <br />
    <v-card v-if="groups.length" class="mx-auto" max-width="400">
      <v-tabs v-model="currentTab" background-color="primary" center-active show-arrows>
        <v-tab :value="item.word_length" v-for="item in groups" :key="item.word_length">
          {{ item.word_length }} letters
        </v-tab>
      </v-tabs>

      <v-card-text>
        <v-window v-model="currentTab">
          <v-window-item :value="item.word_length" v-for="item in groups" :key="item.word_length">
            <v-list :items="item['_items']" item-value="word" item-title="word" />
          </v-window-item>
        </v-window>
      </v-card-text>
    </v-card>
  </div>
</template>

<script setup>
import { ref } from 'vue'
import { getWordsList } from '@/api'

const groups = ref([])
const letters = ref('')
const isLoading = ref(false)
const currentTab = ref(null)
const errorText = ref('')
const showError = ref(false)
const getWords = async () => {
  try {
    currentTab.value = null
    showError.value = false
    groups.value = []
    isLoading.value = true
    const { data } = await getWordsList(letters.value)
    groups.value = data['_groups']
    currentTab.value = data['_groups'][0].word_length
  } catch (error) {
    errorText.value = error
    showError.value = true
  } finally {
    isLoading.value = false
  }
}
</script>

<style scoped lang="scss">
.wf-WordFinder {
  padding: 15px;
  :deep(.v-field__input) {
    padding-bottom: 8px;
  }
}
</style>
